@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Poppins', sans-serif;
}

body {
   display: flex;
   justify-content: center;
   align-items: center;
   min-height: 100vh;
   background: #edf1f4;
}

.login {
   position: relative;
   width: 380px;
   padding: 80px 50px 50px;
   box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.1), -15px -15px 20px #fffb;
   border-radius: 20px;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   gap: 20px;

   form {
      position: relative;
      width: 100%;

      h2 {
         font-size: 2em;
         margin-bottom: 30px;
         line-height: 0.9em;

         span {
            font-weight: 300;
            font-size: 0.65em;
         }
      }

      .input-box {
         position: relative;
         display: flex;

         input {
            border: none;
            outline: none;
            background: transparent;
            border-radius: 10px;
            font-size: 1em;

            &[type='text'],
            &[type='password'] {
               width: 100%;
               padding: 15px 20px 15px 40px;
               margin-bottom: 20px;
               box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.1),
                  inset -5px -5px 10px #fff;
            }

            &[type='submit'] {
               width: 100%;
               margin-top: 20px;
               padding: 15px 20px;
               font-weight: 600;
               box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), -5px -5px 10px #fff;
               cursor: pointer;

               &:focus {
                  box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.1),
                     inset -5px -5px 10px #fff;
               }
            }
         }

         i {
            position: absolute;
            left: 20px;
            top: 25%;
         }
      }

      label {
         display: flex;
         align-items: center;
         cursor: pointer;

         input {
            &[type='checkbox'] {
               margin-right: 5px;
               cursor: pointer;
            }
         }
      }
   }

   .fingerprint {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 15px;

      .fingerprint-box {
         width: 50%;
         padding: 15px 20px 10px;
         box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), -5px -5px 10px #fff;
         border: 3px solid #edf1f4;
         border-radius: 10px;
         background: linear-gradient(315deg, #03a9f4, #0082bc);
         cursor: pointer;

         svg {
            max-width: 40px;
            filter: invert(1);
            animation: animate 3s linear infinite;
         }
      }

      p {
         font-weight: 500;
      }
   }
}

@keyframes animate {
   0%,
   100% {
      opacity: 1;
   }
   50% {
      opacity: 0.5;
   }
}
